import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/'
import NovoEvento from '@/components/NovoEvento/'
import ListaEventos from '@/components/listaEventos/'
import DetalhesEvento from '@/components/DetalhesEvento/'
import ListaConvidados from '@/components/lista/'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/evento/:token',
      name: 'DetalhesEvento',
      component: DetalhesEvento
    },
    {
      path: '/evento/:token/lista',
      name: 'ListaConvidados',
      component: ListaConvidados
    },
    {
      path: '/novo/evento',
      name: 'NovoEvento',
      component: NovoEvento
    },
    {
      path: '/lista/eventos',
      name: 'ListaEventos',
      component: ListaEventos
    }
  ]
})



