<template lang="html">
  <defaultToolbar title="Todos eventos">
    <v-card v-for="event in events" :key="event.id">
      <v-card-text @click="goTo(event.id)">
        {{ event.client }}
      </v-card-text>
    </v-card>
  </defaultToolbar>
</template>

<script>
import defaultToolbar from '@/components/DefaultToolbar'
import axios from 'axios'

export default {
  components: {
    defaultToolbar
  },
  data () {
    return {
      events: []
    }
  },
  methods: {
    goTo (value) {
      this.$router.push('/evento/' + value)
    }
  },
  async mounted () {
    const { data: result } = await axios.get('https://lista-buffet.firebaseio.com/events/0/0.json')
    this.events = result
  }
}
</script>

<style lang="css">
</style>



