import Vue from 'vue'
import Vuex from 'vuex'
// import handler from 'vuex-handler'
// import Raven from 'raven-js'

// import home from './home'
import list from './list'
import menu from './menu'
import snackbar from './snackbar'

Vue.use(Vuex)

const store = new Vuex.Store({
  // plugins: [ handler ],
  modules: {
    menu,
    list,
    snackbar
  }
})
// const doNothing = {
//   onSuccess (result, type, params, options) {
//     return result
//   },
//   onFailure () {
//   }
// }

// store.handler = {
//   onSuccess (result, type, params, options) {
//     return result
//   },
//   onFailure (error, type, params, options) {
//     console.log(error)
//     store.commit('loadingScreen/SET_ERROR', {
//       action: type,
//       visibility: true
//     }, {root: true})
//     Raven.captureException(error, { logger: `${type}` })
//     return false
//   },
//   'shared/changeUnit': doNothing,
//   'shared/getInfo': doNothing,
//   'booking/setDate': doNothing
// }

if (module.hot) {
  module.hot.accept(['./menu', './list', './snackbar'], () => {
    const menu = require('./menu').default
    const list = require('./list').default
    const snackbar = require('./snackbar').default
    // swap in the new actions and mutations
    store.hotUpdate({
      modules: {
        menu,
        list,
        snackbar
      }
    })
  })
}

export default store



