<template>
  <v-container>
    <v-layout row>
      <v-flex>
        <v-text-field class="show-progress" label="Nome" v-model="guestName" :loading="loadingGuest" hide-details solo>
          <v-progress-linear slot="progress" color="blue lighten-2" height="2" indeterminate></v-progress-linear>
        </v-text-field>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex mt-4 style="text-align:center">
        <v-btn dark color="blue lighten-2" @click="addGuest">
          adicionar
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { db } from '../../realtime'

export default {
  data () {
    return {
      guestName: '',
      guestAttendance: true,
      tabs: 0,
      loadingGuest: false,
      message: false
    }
  },
  methods: {
    addGuest () {
      if (this.guestName === '' || this.loadingGuest) return
      this.loadingGuest = true
      this.$firebaseRefs.guestsRefs.push({
        name: this.guestName,
        attendance: this.guestAttendance
      })
      setTimeout(() => {
        this.resetGuest()
      }, 500)
    },
    resetGuest () {
      this.guestName = ''
      this.loadingGuest = false
      this.$store.commit('snackbar/SHOW_MESSAGE', {
        text: 'Convidado adicionado'
      })
    }
  },
  firebase: {
    guestsRefs: db.ref('events/0/0/0/guests')
  }
}
</script>
<style>
.show-progress.input-group--async-loading .input-group__details{
  display: flex!important;
  position: absolute;
  bottom: 0px;
}
</style>



