<template lang="html">
  <defaultToolbar title="Detalhes">
    <v-card>
      <v-card-text v-if="eventDetail !== null">
        {{ eventDetail.client }}
        {{ eventDetail.date }}
        <v-container>
          <v-layout row wrap>
            <v-flex>
              <img src="static/img/guests.png" alt="">
            </v-flex>
            <v-flex>
              10/120
            </v-flex>
          </v-layout>
        </v-container>
        <h1>
          ir para a <router-link to="/evento/0/lista">lista</router-link>
        </h1>
      </v-card-text>
    </v-card>
  </defaultToolbar>
</template>

<script>
import defaultToolbar from '@/components/DefaultToolbar'
import axios from 'axios'

export default {
  components: {
    defaultToolbar
  },
  data () {
    return {
      eventDetail: null
    }
  },
  async mounted () {
    const { data: result } = await axios.get('https://lista-buffet.firebaseio.com/events/0/0/0.json')
    this.eventDetail = result
  }
}
</script>

<style lang="css">
</style>



