<template>
  <v-navigation-drawer v-model="menu" fixed app>
    <v-list dense>
      <v-list-tile  avatar>
        <v-list-tile-avatar color="grey">
          <v-icon color="grey darken-2">person</v-icon>
        </v-list-tile-avatar>
        <v-list-tile-content>
          <v-list-tile-title>Renato Vicente Frison</v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-icon>settings</v-icon>
        </v-list-tile-action>
      </v-list-tile>
      <v-list-tile>
        <v-list-tile-action>
          <v-icon>home</v-icon>
        </v-list-tile-action>
        <v-list-tile-content>
          <v-list-tile-title>Inicio</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
      <v-list-group v-model="dropdown" no-action>
          <v-list-tile slot="activator">
            <v-list-tile-action>
              <v-icon>alarm</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>Neste dia</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile @click="">
            <v-list-tile-content>
              <v-list-tile-title>Renato e Fabiana</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile @click="">
            <v-list-tile-content>
              <v-list-tile-title>Gustavo e Denise</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list-group>
        <v-list-tile>
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Sair</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
// import VCardExpansion from './VCardExpansion.vue'
export default {
  // components: {
  //   VCardExpansion
  // },
  data () {
    return {
      dropdown: false
    }
  },
  methods: {
    ...mapMutations('menu', ['TOGGLE_MENU'])
  },
  computed: {
    ...mapGetters('menu', ['open']),
    menu: {
      get () {
        return this.open
      },
      set (value) {
        this.TOGGLE_MENU(value)
      }
    }
  }
}
</script>
<style>
</style>



