<template>
  <div>
    <v-text-field
    flat
    solo
    prepend-icon="search"
    label="Procurar..."
    v-model="search"
    class="grey lighten-3"
    ></v-text-field>
    <v-list subheader class="center-switch">
      <div v-if="guests.length === 0">
        <v-list-tile class="pt-2">
          <v-list-tile-content>
            <v-list-tile-title style="text-align:center">Nenhum nome encontrado</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </div>
      <div class="" v-for="(guest, index) in guests" :key="guest['.key']">
        <v-subheader v-if="index === 0">{{ guest.name[0].toUpperCase() }}</v-subheader>
        <v-subheader v-else-if="compareInitials(guest, guests[index - 1])">{{ guest.name[0].toUpperCase() }}</v-subheader>
        <v-list-tile  avatar @click="">
          <v-list-tile-avatar color="grey darken-2">
            <img :src="guest.avatar" alt="" v-if="guest.avatar">
            <span style="color:#ffffffb3" v-else>{{ guest.name[0].toUpperCase() }}</span>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>{{ guest.name }}</v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-action>
            <v-switch hide-details :input-value="guest.attendance" @change="toggleAttendance(guest['.key'], $event)"></v-switch>
          </v-list-tile-action>
        </v-list-tile>
      </div>
    </v-list>
  </div>
</template>

<script>
import { db } from '../../realtime'

export default {
  data () {
    return {
      search: ''
    }
  },
  methods: {
    compareInitials (actual, last) {
      return last.name[0].toUpperCase() !== actual.name[0].toUpperCase()
    },
    toggleAttendance (key, payload) {
      this.$firebaseRefs.guestsRefs.child(key).update({ attendance: payload })
    }
  },
  computed: {
    guestsToUpper () {
      return this.guestsRefs.sort((a, b) => (a.name.toUpperCase() > b.name.toUpperCase()) ? 1 : ((b.name.toUpperCase() > a.name.toUpperCase()) ? -1 : 0))
    },
    guests () {
      if (this.search === '') {
        return this.guestsToUpper
      } else {
        const searchTerm = this.search.toUpperCase()
        return this.guestsToUpper.filter(elem => {
          return elem.name.toUpperCase().includes(searchTerm)
        })
      }
    }
  },
  firebase: {
    guestsRefs: db.ref('events/0/0/0/guests')
  }
}
</script>
<style>
.center-switch .list__tile__action{
  justify-content: center!important;
}
</style>



