<template lang="html">
  <defaultToolbar title="Novo">
    <v-card>
      <v-card-text>
        <v-text-field
          v-model="event.name"
          :rules="[() => event.name.length > 0 || 'This field is required']"
          label="Legal first name"
          class="mt-5"
          required
        ></v-text-field>
      </v-card-text>
    </v-card>
  </defaultToolbar>
</template>

<script>
import defaultToolbar from '@/components/DefaultToolbar'

export default {
  components: {
    defaultToolbar
  },
  data () {
    return {
      event: {
        name: 'Fabiana e Renato',
        date: '01/02/2018',
        place: {
          id: '0',
          text: 'Status amazonas'
        }
      }
    }
  }
}
</script>

<style lang="css">
</style>



