<template lang="html">
  <div class="">
    <v-toolbar color="light-blue" dark fixed>
      <v-btn icon @click="back">
        <v-icon>arrow_back</v-icon>
      </v-btn>
      <v-toolbar-title>{{ title }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <slot name="icon-right"></slot>
    </v-toolbar>
    <v-layout row>
      <v-flex xs12>
        <v-card class="mt-5">
          <slot/>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="css">
.toolbar__content,.toolbar__content {
  height: 48px!important;
}
</style>



