export default {
  SET_SEARCH (state, payload) {
    state.search = payload
  },
  SET_SHOW_SEARCH (state, payload) {
    state.showSearch = payload
  },
  CLEAR_SEARCH (state) {
    state.search = ''
    state.showSearch = false
  }
}



