<template>
  <div class="pt-3 guestDrawer">
    <v-tabs grow v-model="tabs" hide-slider>
      <v-tab :key="0" :ripple="false">
        <img src="static/img/presents.png" alt="" class="mb-2">
        <div class="">
          Convidado
        </div>
      </v-tab>
      <v-tab :key="1" :ripple="false">
        <img src="static/img/dj.png" alt="" class="mb-2">
        <div class="">
          Profissional
        </div>
      </v-tab>
      <v-tab-item :key="0">
        <newGuest/>
      </v-tab-item>
      <v-tab-item :key="1">
        <newWorker/>
      </v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
import newGuest from './newGuest'
import newWorker from './newWorker'

export default {
  components: {
    newWorker,
    newGuest
  },
  data () {
    return {
      tabs: 0
    }
  }
}
</script>

<style>
.guestDrawer .tabs__container{
  height: 115px;
}
.guestDrawer .tabs__item{
  flex-direction: column;
}
.guestDrawer .tabs__item img {
  filter: grayscale();
  transition: filter .4s cubic-bezier(.86,0,.07,1)
}
.guestDrawer .tabs__item.tabs__item--active img{
  filter: grayscale(0);
}
</style>



