<template>
  <v-container>
    <v-layout row>
      <v-flex>
        <v-text-field class="show-progress" label="Empresa" v-model="company" :loading="loading" hide-details solo>
          <v-progress-linear slot="progress" color="blue lighten-2" height="2" indeterminate></v-progress-linear>
        </v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row pt-2>
      <v-flex style="text-align:center">
        <v-btn flat icon color="blue lighten-2" class="ma-0" @click="count > 1 ? count -= 1 : null">
          <v-icon>remove</v-icon>
        </v-btn>
        <v-btn flat color="grey" class="ma-0">
          {{ count }}
        </v-btn>
        <v-btn flat icon color="blue lighten-2" class="ma-0" @click="count += 1">
          <v-icon>add</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>
    <v-flex mt-2 style="text-align:center">
      <v-btn dark color="blue lighten-2" @click="addWorkers">
        adicionar
      </v-btn>
    </v-flex>
</v-container>
</template>

<script>
import { db } from '../../realtime'

export default {
  data () {
    return {
      count: 1,
      company: '',
      loading: false
    }
  },
  methods: {
    addWorkers () {
      if (this.company === '' || this.loading) return
      this.loading = true
      this.$firebaseRefs.workersRef.push({
        company: this.company,
        count: this.count
      })
      setTimeout(() => {
        this.resetWorkers()
      }, 500)
    },
    resetWorkers () {
      this.company = ''
      this.loading = false
      this.count = 1
    }
  },
  firebase: {
    guestsRefs: db.ref('events/0/0/0/guests')
  }
}
</script>
<style>
.show-progress.input-group--async-loading .input-group__details{
  display: flex!important;
  position: absolute;
  bottom: 0px;
}
</style>



