<template lang="html">
  <div class="">
    <defaultToolbar title="Renato e Fabiana">
      <BaseList/>
      <v-btn icon @click.native="drawer = true" slot="icon-right">
        <v-icon>person_add</v-icon>
      </v-btn>
    </defaultToolbar>
    <v-navigation-drawer v-model="drawer" right app width="250">
      <NovoConvidado/>
    </v-navigation-drawer>
  </div>
</template>

<script>
import DefaultToolbar from '@/components/DefaultToolbar'
import NovoConvidado from '@/components/NovoConvidado/'
import BaseList from './BaseList'

export default {
  components: {
    DefaultToolbar,
    BaseList,
    NovoConvidado
  },
  data () {
    return {
      drawer: false
    }
  }
}
</script>

<style lang="css">
</style>



